import { Injectable } from '@angular/core';
import { SegmentType } from '../coderulemetadata/base/segment/segmenttype';

@Injectable()

export class CodeRuleService {
    
    /**
     * GuidId
     */
    getGuid() {
        return (this.s4() + this.s4() + "-" + this.s4() + "-" + this.s4() + "-" + this.s4() + "-" + this.s4() + this.s4() + this.s4());
    }

    /**
     * 4
     */
    private s4() {
        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    }

    //
    deleteSpecifiedElement(inputArray: Array<any>, element: any) {
        let tempIndex = inputArray.findIndex(value => value == element);
        if (tempIndex != (-1)) {
            inputArray.splice(tempIndex, 1);
        }
    }

    //
    getSelectedList(value: any, input: any, inputArray: Array<any>) {
        if (value==true) {
            inputArray.push(input);
        }
        else {
            let tempIndex = inputArray.findIndex(value => value == input);
            if (tempIndex != (-1)) {
                inputArray.splice(tempIndex, 1);
            }
        }
    }

    //id
    replaceSpecifiedElementById(inputArray: Array<any>, element: any) {
        let tempIndex = inputArray.findIndex(value => value.id == element.id);
        if (tempIndex != (-1)) {
            inputArray.splice(tempIndex, 1, element);
        }
    }

    replaceSpecifiedElementByCode(inputArray: Array<any>, element: any) {
        let tempIndex = inputArray.findIndex(value => value.Code == element.Code);
        if (tempIndex != (-1)) {
            inputArray.splice(tempIndex, 1, element);
        }
    }


    //Code
    deleteSpecifiedElementByCode(inputArray: Array<any>, element: any) {
        let tempIndex = inputArray.findIndex(value => value.Code == element.Code);
        if (tempIndex != (-1)) {
            inputArray.splice(tempIndex, 1);
        }
    }

        /**
     * 
     * @param segmentType 
     */
    getSegmentTypeToString(segmentType:SegmentType):string{
        switch(segmentType){
            case SegmentType.Constant:
            return 'Constant';
            case SegmentType.OutParam:
            return 'OutParam';
            case SegmentType.Sequence:
            return 'Sequence';
            case SegmentType.SysVariable:
            return 'SysVariable';
            default:
            return null;
        }
    }

     /**
     * 
     * @param segmentType 
     */
    getSegmentTypeDesc(segmentType:string):string{
        switch(segmentType){
            case 'Constant':
            return '';
            case 'OutParam':
            return '';
            case 'Sequence':
            return '';
            case 'SysVariable':
            return '';
            default:
            return null;
        }
    }
}


